<template>
  <v-sheet color="grey darken-3" dark height="100%" class="py-5">
    <v-form ref="form" v-model="valid">
      <v-container>
        <v-row class="text-center">
          <v-col cols="12">
            <v-img
              :src="require('../assets/logo.png')"
              contain
              max-width="250"
              class="mx-auto"
            />
          </v-col>
          <v-col>
            <v-textarea
              :rules="contentRules"
              required
              outlined
              v-model="form.content"
              name="input-7-4"
              label="情緒溫度留言"
              value="The Woodman set to work at once, and so sharp was his axe that the tree was soon chopped nearly through."
            ></v-textarea>
          </v-col>
          <v-col cols="12">
            <v-btn
              :disabled="!valid"
              x-large
              color="success"
              dark
              @click.prevent="PostContent"
            >
              上傳
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
    <v-dialog v-model="dialog.open" width="500">
      <v-card class="grey" dark>
        <v-card-title class="headline grey daken-2">
          {{ dialog.title }}
        </v-card-title>

        <v-card-text> {{ dialog.text }}</v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog.open = false">
            關閉
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-sheet>
</template>

<script>
// import HelloWorld from "../components/HelloWorld";
const methods = {
  PostContent() {
    axios
      .post("/PostContent", this.form)
      .then((response) => {
        if (response.data == "success") {
          this.dialog.open = true;
          this.dialog.title = "感謝您";
          this.dialog.text = "上傳成功!";
        }
      })
      .catch((err) => {
        this.dialog.open = true;
        this.dialog.title = "T_T";
        this.dialog.text = "現在好像有點塞車，晚點再試試!";
      });
  },
};

export default {
  data() {
    return {
      valid: true,
      contentRules: [
        (v) => !!v || "請填寫一些東西哦!",
        (v) => (v && v.length >= 5) || "多打一些字啦!",
        (v) => (v && v.length <= 25) || "你打這麼多字幹麻啦!",
      ],
      form: {
        name: "",
        content: "",
      },
      dialog: {
        open: false,
        status: "",
        title: "感謝您",
        text: "",
      },
    };
  },
  name: "Home",
  components: {},
  methods,
};
</script>
